.box {
  background-color: white;
  border: none;
  border-radius: 10%;
  box-shadow: 0px 0px 8px #888;
  width: 5rem;
  height: 5rem;
  text-align: center;
  font-size: 5em;
  font-family: sans-serif;
  font-weight: bold;
  line-height: 5rem;
  margin: 0.5rem;
}
.x {
  color: rgb(245, 50, 15);
}
.o {
  color: rgb(0, 0, 0);
}
.box:hover {
  box-shadow: 0px 0px 15px #888;
}
